<template>
    <div class="graficos">
        <v-toolbar
            color="blue darken-3"
            dark
        >
            <v-avatar size="50px" tile>
                <img
                src="../components/logo.png"
                alt="Nomu"
                >
            </v-avatar>

            <v-toolbar-title class="white--text">Nomu Graphics</v-toolbar-title>

            <v-spacer></v-spacer>

            <v-icon>timeline</v-icon>
        </v-toolbar>

        <v-container grid-list-md text-xs-center>
            <v-layout row wrap>
                <v-flex xs12>
                    <v-card tile>
                        <v-toolbar
                            color="blue darken-3"
                            dark
                        >
                        <v-toolbar-title>Intervalo de Medições</v-toolbar-title>
                        <v-spacer></v-spacer>
                        <v-btn 
                            icon
                            @click="changeData"
                        >
                            <v-icon>done_outline</v-icon>
                        </v-btn>
                        </v-toolbar>
                        <v-menu
                            ref="menu1"
