                            v-model="menu1"
                            :nudge-right="40"
                            lazy
                            transition="scale-transition"
                            offset-y
                            max-width="290px"
                            min-width="290px"
                        >
                            <v-text-field
                                slot="activator"
                                v-model="dateFormatted1"
                                label="De"
                                prepend-icon="event"
                            ></v-text-field>
                            <v-date-picker v-model="date1" no-title @input="menu1 = false"></v-date-picker>
                        </v-menu>

                        <v-menu
                            ref="menu2"
                            :close-on-content-click="false"
                            v-model="menu2"
                            :nudge-right="40"
                            lazy
                            transition="scale-transition"
                            offset-y
                            max-width="290px"
                            min-width="290px"
                        >
                            <v-text-field
                                slot="activator"
                                v-model="dateFormatted2"
                                label="Até"
                                prepend-icon="event"
                            ></v-text-field>
                            <v-date-picker v-model="date2" no-title @input="menu2 = false"></v-date-picker>
                        </v-menu>
                    </v-card>
                </v-flex>

                <v-flex xs6>
                    <v-card>
                        <ve-line :data="pH">
                            <h1>{{ this.message('ph') }}</h1>
                        </ve-line>
                        <v-btn
                            :loading="loading1"
                            :disabled="loading1"
                            color="blue darken-4"
                            class="white--text"
                            @click="loader = 'loading1'"
                        >
                            Exportar
                            <v-icon right dark>get_app</v-icon>
                        </v-btn>
                    </v-card>
                </v-flex>

                <v-flex xs6>
                    <v-card>
                        <ve-line :data="EC">
                            <h1>{{ this.message('ec') }}</h1>
                        </ve-line>
                        <v-btn
                            :loading="loading2"
                            :disabled="loading2"
                            color="blue darken-4"
                            class="white--text"
                            @click="loader = 'loading2'"
                        >
                            Exportar
                            <v-icon right dark>get_app</v-icon>
                        </v-btn>
                    </v-card>
                </v-flex>

                <v-flex xs6>
                    <v-card>
                        <ve-line :data="OD">
                            <h1>{{ this.message('od') }}</h1>
                        </ve-line>
                        <v-btn
                            :loading="loading3"
                            :disabled="loading3"
                            color="blue darken-4"
                            class="white--text"
                            @click="loader = 'loading3'"
                        >
                            Exportar
                            <v-icon right dark>get_app</v-icon>
                        </v-btn>
                    </v-card>
                </v-flex>

                <v-flex xs6>
                    <v-card>
                        <ve-line :data="TURB">
                            <h1>{{ this.message('turb') }}</h1>
                        </ve-line>
                        <v-btn
                            :loading="loading4"
                            :disabled="loading4"
                            color="blue darken-4"
                            class="white--text"
                            @click="loader = 'loading4'"
                        >
                            Exportar
                            <v-icon right dark>get_app</v-icon>
                        </v-btn>
                    </v-card>
                </v-flex>

                <v-flex xs6>
                    <v-card>
                        <ve-line :data="NIVEL">
                            <h1>{{ this.message('nivel') }}</h1>
                        </ve-line>
                        <v-btn
                            :loading="loading5"
                            :disabled="loading5"
                            color="blue darken-4"
                            class="white--text"
                            @click="loader = 'loading5'"
                        >
                            Exportar
                            <v-icon right dark>get_app</v-icon>
                        </v-btn>
                    </v-card>
                </v-flex>

                <v-flex xs6>
                    <v-card>
                        <ve-line :data="TEMP">
                            <h1>{{ this.message('temp') }}</h1>
                        </ve-line>
                        <v-btn
                            :loading="loading6"
                            :disabled="loading6"
                            color="blue darken-4"
                            class="white--text"
                            @click="loader = 'loading6'"
                        >
                            Exportar
                            <v-icon right dark>get_app</v-icon>
                        </v-btn>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>

        <v-footer
            dark
            height="auto"
        >
            <v-card
            class="flex"
            flat
            tile
            >
            <v-card-actions class="grey darken-3 justify-center">
                &copy;2019 — <strong>Nomu</strong>
            </v-card-actions>
            </v-card>
        </v-footer>
    </div>
</template>

<script>
    import VeLine from 'v-charts/lib/line.common'
    import { db } from '../main'

    export default{
        data (){
            return{
                title:'Graficos',
                pH: {
                    columns: ['Date', 'pH'],
                    rows : []
                },
                EC: {
                    columns: ['Date', 'Condutividade'],
                    rows: []
                },
                OD: {
                    columns: ['Date', 'Oxigenio'],
                    rows: []
                },
                TURB: {
                    columns: ['Date', 'Turbidez'],
                    rows: []
                },
                NIVEL: {
                    columns: ['Date', 'Nivel'],
                    rows: []
                },
                TEMP: {
                    columns: ['Date', 'Temperatura'],
                    rows: []
                },
                vals: [],
                loader: null,
                loading1: false,
                loading2: false,
                loading3: false,
                loading4: false,
                loading5: false,
                loading6: false,
                menu1: false,
                menu2: false,
                date1: new Date().toISOString().substr(0, 10),
                date2: new Date().toISOString().substr(0, 10),
                dateFormatted1: '',
                dateFormatted2: ''
            }
        },
        firestore (){
            return{
                valspH: db.collection(this.$route.params.Pid).doc('pH').collection('medições'),
                valsEc: db.collection(this.$route.params.Pid).doc('Condutividade').collection('medições'),
                valsOd: db.collection(this.$route.params.Pid).doc('Oxigenio').collection('medições'),
                valsTu: db.collection(this.$route.params.Pid).doc('Turbidez').collection('medições'),
                valsNi: db.collection(this.$route.params.Pid).doc('Nivel').collection('medições'),
                valsTe: db.collection(this.$route.params.Pid).doc('Temperatura').collection('medições')
            }
        },
        mounted () {
            this.changeData()
        },
        components: { 
            VeLine 
        },
        methods: {
            changeData: function() {
                var num;

                this.pH.rows = [];
                for(num = 0; num < this.valspH.length; num++){
                    var date1 = new Date(this.valspH[num].Date.split(" ")[0]);
                    var date2 = new Date(this.parseDate(this.dateFormatted1));
                    var date3 = new Date(this.parseDate(this.dateFormatted2));

                    if(+date1 >= + date2 && +date1 <= +date3){
                        this.pH.rows[num] = this.valspH[num];
                    }
                }

                this.EC.rows = [];
                for(num = 0; num < this.valsEc.length; num++){
                    var date1 = new Date(this.valsEc[num].Date.split(" ")[0]);
                    var date2 = new Date(this.parseDate(this.dateFormatted1));
                    var date3 = new Date(this.parseDate(this.dateFormatted2));

                    if(+date1 >= + date2 && +date1 <= +date3){
                        this.EC.rows[num] = this.valsEc[num];
                    }
                }

                this.OD.rows = [];
                for(num = 0; num < this.valsOd.length; num++){
                    var date1 = new Date(this.valsOd[num].Date.split(" ")[0]);
                    var date2 = new Date(this.parseDate(this.dateFormatted1));
                    var date3 = new Date(this.parseDate(this.dateFormatted2));

                    if(+date1 >= + date2 && +date1 <= +date3){
                        this.OD.rows[num] = this.valsOd[num];
                    }
                }

                this.TURB.rows = [];
                for(num = 0; num < this.valsTu.length; num++){
                    var date1 = new Date(this.valsTu[num].Date.split(" ")[0]);
                    var date2 = new Date(this.parseDate(this.dateFormatted1));
                    var date3 = new Date(this.parseDate(this.dateFormatted2));

                    if(+date1 >= + date2 && +date1 <= +date3){
                        this.TURB.rows[num] = this.valsTu[num];
                    }
                }

                this.NIVEL.rows = [];
                for(num = 0; num < this.valsNi.length; num++){
                    var date1 = new Date(this.valsNi[num].Date.split(" ")[0]);
                    var date2 = new Date(this.parseDate(this.dateFormatted1));
                    var date3 = new Date(this.parseDate(this.dateFormatted2));

                    if(+date1 >= + date2 && +date1 <= +date3){
                        this.NIVEL.rows[num] = this.valsNi[num];
                    }
                }

                this.TEMP.rows = [];
                for(num = 0; num < this.valsTe.length; num++){
                    var date1 = new Date(this.valsTe[num].Date.split(" ")[0]);
                    var date2 = new Date(this.parseDate(this.dateFormatted1));
                    var date3 = new Date(this.parseDate(this.dateFormatted2));

                    if(+date1 >= + date2 && +date1 <= +date3){
                        this.TEMP.rows[num] = this.valsTe[num];
                    }
                }
            },
            message(opc){
                if(opc == 'ph'){
                    if(this.pH.rows.length == 0) return 'Nenhuma medição para este intervalo!';
                    else return '';
                }
                else if(opc == 'ec'){
                    if(this.EC.rows.length == 0) return 'Nenhuma medição para este intervalo!';
                    else return '';
                }
                else if(opc == 'od'){
                    if(this.OD.rows.length == 0) return 'Nenhuma medição para este intervalo!';
                    else return '';
                }
                else if(opc == 'turb'){
                    if(this.TURB.rows.length == 0) return 'Nenhuma medição para este intervalo!';
                    else return '';
                }
                else if(opc == 'nivel'){
                    if(this.NIVEL.rows.length == 0) return 'Nenhuma medição para este intervalo!';
                    else return '';
                }
                else if(opc == 'temp'){
                    if(this.TEMP.rows.length == 0) return 'Nenhuma medição para este intervalo!';
                    else return '';
                }
            },
            formatDate (date) {
                if (!date) return null

                const [year, month, day] = date.split('-')
                return `${day}/${month}/${year}`
            },
            parseDate (date) {
                if (!date) return null

                const [day, month, year] = date.split('/')
                return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
            }
        },
        watch: {
            loader () {
                const l = this.loader
                this[l] = !this[l]

                setTimeout(() => (this[l] = false), 3000)

                this.loader = null
            },
            date1 (val) {
                this.dateFormatted1 = this.formatDate(this.date1)
            },
            date2 (val) {
                this.dateFormatted2 = this.formatDate(this.date2)
            }
        }
    }
</script>

<style>
  .custom-loader {
    animation: loader 1s infinite;
    display: flex;
  }
  @-moz-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @-o-keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
  @keyframes loader {
    from {
      transform: rotate(0);
    }
    to {
      transform: rotate(360deg);
    }
  }
</style>